export default {
    name: "memberover",
    data(){
        return{
            member:[],
            state:""

        }
    },
    mounted:function () {
        this.$axios.get('/user/card').then(res=>{
            this.member=res.data.data;
            this.state=res.data.data.status;
            if(this.state=="0"){
                this.state="过期"
            }else if(this.state=="1"){
                this.state="正常"
            }
        })
    }
}



